<template>
  <div class="loadding">
      <img width="24" height="24" src="./loading.gif" alt="">
      <p class="desc">{{title}}</p>
  </div>
</template>
<script>
export default {
  name:"loadding",
  props:{
      title:{
          type:String,
          default:'拼命加载中...'
      }
  }
}
</script>
<style lang="stylus" scoped>
@import '~common/stylus/variable'
    .loadding
        width:100%
        text-align:center
        .desc
            line-height:20px
            font-size:$font-size-small
            color:$color-text-l
</style>




